.photo-gallery{
	display: grid;
	grid-template-columns: repeat(3, 250px);
	grid-auto-rows: 250px;
	gap: 20px;
	grid-auto-flow: dense;

	.gallery-photo{
		max-width: 100%;
		grid-column: span 1;
		grid-row: span 1;

		img{
			aspect-ratio: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&.grid-column-2{
			grid-column: span 2;
		}
		&.grid-column-3{
			grid-column: span 3;
		}
		&.grid-row-2{
			grid-row: span 2;
		}
		&.grid-row-3{
			grid-row: span 3;
		}
	}

}